import React from 'react';
import { useAuth } from './AuthContext'; // Importa el contexto de autenticacin
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import { URL_API } from '../const';

const endpoint = `${URL_API}/logout`;

const SignOut = () => {
  const { logout } = useAuth();
  const navigate = useNavigate();
  const handleSignOut = async () => {
    try {
      // Realiza una solicitud al backend para revocar el token
      //await axios.post(endpoint, {

        // Puedes enviar cualquier informacin adicional que tu backend necesite
      //});

      // Luego, llama a la funcin de cierre de sesin del contexto
      localStorage.removeItem('token');
      logout();
      navigate('/');
      window.location.reload();
      
    } catch (error) {
      console.error('Error al cerrar sesin:', error);
      // Maneja errores si es necesario
    }
  };

  return (
    <div>
      <h2>Ests seguro de que deseas cerrar sesin?</h2>
      <button onClick={handleSignOut}>Cerrar Sesin</button>

    </div>
  );
};

export default SignOut;
